<template>
  <div class="wdShouyi">
    <ul class="tab_list">
      <li @click="changedt()">
        <i :class="(searchInfo.sort===1||searchInfo.sort===2) ? 'active' : ''">时间</i>
        <div>
          <span class="iconfont icon-shangsanjiao" :class="searchInfo.sort===1 ? 'active' : ''"></span>
          <span class="iconfont icon-xiasanjiao" :class="searchInfo.sort===2 ? 'active' : ''"></span>
        </div>
      </li>
      <li @click="changemn()">
        <i :class="(searchInfo.sort===3||searchInfo.sort===4)  ? 'active' : ''">金额</i>
        <div>
          <span class="iconfont icon-shangsanjiao" :class="searchInfo.sort===4 ? 'active' : ''"></span>
          <span class="iconfont  icon-xiasanjiao" :class="searchInfo.sort===3 ? 'active' : ''"></span>
        </div>
      </li>
      <li @click="popupVisible=true" :class="searchInfo.sort===5 ? 'active' : ''"><i>筛选</i> </li>
    </ul>
    <MyLoadMore :list='scorelist'  @is-loadmore = 'loadmore' :isLoad='isLoad'>
    <ul class="jf_list">
      <li v-for="(item,index) in scorelist" :key="index" @click="toDetail(item.id)">
        <div class="jf_detail">
          <div class="jfd_good">
            {{item.title}}
          </div>
          <div class="jfd_num jfd_zhuchu" :style="{'color':item.data_type==='1'?'#CC1C1C':'#3C7FEE'}">
            {{item.data_type==='1'?'+':'-'}}{{item.points}}
          </div>
        </div>
        <div class="jf_data">
          <div class="jfd_year">{{item.created_at}}</div>
          <!-- <div :class="{jfd_state:true,jfd_deal:!item.iscome}">{{item.state}}</div> -->
        </div>
      </li>
    </ul>
    </MyLoadMore>
    <van-button type="default invate" @click="toRecharge">充值</van-button>
    <mt-popup
      v-model="popupVisible"
      position="right"
    >
      <div class="po_warp">
        <form @submit.prevent="popupVisible=false;searchInfo.sort=5;getScoreList()">
          <div class="bottom_type">
            <van-button type="default" @click.prevent="resetSearch">重置</van-button>
            <van-button type="default" native-type="submit">确认</van-button>
          </div>
          <div class="info">时间</div>
          <div class="time_area">
            <div class="low_time" @click="$refs.pickerone.open()">{{searchInfo.min_ymd}}<i class="iconfont icon-rili"></i></div>
            <span></span>
            <div class="high_time" @click="$refs.pickertwo.open()">{{searchInfo.max_ymd}}<i class="iconfont icon-rili"></i></div>
          </div>
          <div class="info">金额区间</div>
          <div class="money_area">
            <input type="number" min="0" v-model="searchInfo.min_points" placeholder="最低金额">
            <span></span>
            <input type="number" :min="searchInfo.min_points" v-model="searchInfo.max_points" placeholder="最高金额">
          </div>
          <div class="info">类别</div>
          <div>
            <ul class="type_list">
              <li v-for="(item,index) in type_list" :key="index" :style="{'color':searchInfo.type===item.status?'#3C7FEE':''}" @click="()=>{$set(searchInfo,'type',item.status)}">
                {{item.typeName}}
              </li>
            </ul>
          </div>
        </form>
      </div>
    </mt-popup>
    <a href="" class="kefu iconfont icon-kefu"></a>
    <mt-datetime-picker
      ref="pickerone"
      type="date"
      year-format="{value} 年"
      month-format="{value} 月"
      date-format="{value} 日"
      :startDate="new Date('2000/1/1')"
      :endDate="new Date()"
      @confirm="handleConfirmone">
    </mt-datetime-picker>
    <mt-datetime-picker
      ref="pickertwo"
      type="date"
      year-format="{value} 年"
      month-format="{value} 月"
      date-format="{value} 日"
      :startDate="new Date(searchInfo.min_ymd)"
      :endDate="new Date()"
      @confirm="handleConfirmtwo">
    </mt-datetime-picker>
  </div>
</template>

<script>
import { apiGet } from '../common/js/api';
import  MyLoadMore  from '@/common/MyLoadMore'
  export default {
    name: "jf_mingxi",
    components:{ MyLoadMore },
    data() {
      return {
        show: false,
        lowDate:false,
        isLoad:true,
        searchInfo:{
          // timeSort:-1,//0表示时间正序，1倒序,-1表示不参加条件
          // moneySort:-1,//0表示金额正序，1倒序， -1表示不参加条件
          sort:"",
          type:"",//类型
          page:1,
          size:10,

        },
        type_list:[
          {
            typeName:"首佣",
            status:7
          },
          {
            typeName:"续佣",
            status:6
          },
          {
            typeName:"增员奖",
            status:5
          },
          {
            typeName:"管理奖",
            status:4
          },
          {
            typeName:"充值",
            status:3
          },
          {
            typeName:"兑换",
            status:8
          },
        ],
        popupVisible: false,
        scorelist:[],
        resData:{}
      }
    },
    created(){
      this.getScoreList()
      apiGet("/index/sign-package",{
        url:encodeURIComponent("http://anfum.demo.sclonsee.com/jfMingxi")
      }).then(res=>{
        let data2 = res.data.data.signpackage;
        this.resData = data2;
          wx.config({
          debug: true, // 开启调试模式,开发时可以开启  
          appId: data2.appId,   // 必填，公众号的唯一标识   由接口返回
          timestamp: data2.timestamp, // 必填，生成签名的时间戳 由接口返回
          nonceStr: data2.nonceStr,    // 必填，生成签名的随机串 由接口返回
          signature: data2.signature,   // 必填，签名 由接口返回
          jsApiList: ["onMenuShareAppMessage"] // 此处填你所用到的方法 
        });
      })
    },
    methods: {
      //获取我的收益列表
      getScoreList(){
        console.log(this.searchInfo)
        apiGet("/integral/integral-list",{
          ...this.searchInfo,
           accessToken:this.$cookie.get('accessToken'),
          // accessToken:"3HzxiZdbTu1dcWkJN96N_TULIae8DIZ_",
        }).then(res=>{
          console.log(res)
          this.scorelist = res.data.data.data;
        })
      },
      //充值
      toRecharge(){
        const {resData} = this
        alert(`https://open.weixin.qq.com/connect/oauth2/authorize?appid=${resData.appId}&redirect_uri=http://anfuapi.demo.sclonsee.com/v1/index/get-code&response_type=code&scope=snsapi_base#wechat_redirect`)
        window.location.href=`https://open.weixin.qq.com/connect/oauth2/authorize?appid=${resData.appId}&redirect_uri=http%3A%2F%2Fanfum.demo.sclonsee.com%2FjfCharge&response_type=code&scope=snsapi_base#wechat_redirect`
      },
      //加载更多
      loadmore(){
        this.$set(this.searchInfo,'page',++this.searchInfo.page)
        apiGet("/integral/integral-list",{ ...this.searchInfo, accessToken:this.$cookie.get('accessToken'),}).then(res => {
          if (res.status === 200) {
            this.scorelist = this.scorelist.concat(res.data.data.data);
            if(res.data.data.data.length< 10) {
              this.isLoad = false;
            } else {
              this.isLoad = true
            }
          }
        })

      },
      //按时间正反排序
      changedt(){
        this.searchInfo.page=1;
        this.isLoad=true;
        const { sort } = this.searchInfo
        this.$set(this.searchInfo,'sort', sort===1?2:1)
        this.getScoreList()
      },
      //按分数该地排序
      changemn(){
        this.searchInfo.page=1;
        this.isLoad=true;
        const { sort } = this.searchInfo
        this.$set(this.searchInfo,'sort', sort===3?4:3)
        this.getScoreList()
      },
      //重置搜索条件
      resetSearch(){
        this.searchInfo.page=1;
        this.isLoad=true;
        this.popupVisible = false;
        this.searchInfo = {
          // timeSort:-1,//0表示时间正序，1倒序,-1表示不参加条件
          // moneySort:-1,//0表示积分正序，1倒序， -1表示不参加条件
          sort:"",
          page:1,
          size:10,
          type:"",//类型
        };
        this.getScoreList();
      },
      //开始时间
      handleConfirmone (date) {
        console.log(date);
        this.$set(this.searchInfo,'min_ymd',date.toLocaleDateString().replace(/\//g,"-"))
        this.getScoreList()
      },
      //结束时间
      handleConfirmtwo (date) {
        console.log(date);
        this.$set(this.searchInfo,'max_ymd',date.toLocaleDateString().replace(/\//g,"-"))
        this.getScoreList()
      },
      //跳转积分详情
      toDetail(scoreId){
        this.$router.push({
          path:"/jfDetail",
          query:{
            scoreId
          }
        })
      }
    }
  }
</script>

<style scoped lang="scss">
  .active{
    color: #3C7FEE !important;
  }
  .wdShouyi{
    padding-bottom: 0.6rem;
    background: #E6E9ED;
    height: 100%;
    display: flex;
    flex-direction: column;
    .mint-popup-right{
      width: 70%;
      height: 100%;
      background: white;
      .po_warp{
        position: relative;
        height: 100%;
        overflow: auto;
        .info{
          height: 0.31rem;
          background: #efefef;
          line-height: 0.31rem;
          text-indent: 0.13rem;
          font-size: 0.15rem;
          color: #9a9a9a;
        }
        .time_area,.money_area{
          padding-top: 0.21rem;
          overflow: hidden;
          padding-bottom: 0.2rem;
          span{float: left;margin: 0.15rem 0.06rem;width: 0.16rem;height: 1px;background: #bcbcbc}
          .low_time{margin-left: 5%}
          .low_time,.high_time,input{
            width: 40%;
            font-size: 0.13rem;
            color: #9a9a9a;
            line-height: 0.31rem;
            border:1px solid rgba(188,188,188,1);
            border-radius:0.04rem;
            height: 0.31rem;
            text-indent: 0.09rem;
            float: left;
            i{float: right;color: #3C7FEE;font-size: 0.15rem;margin-right: 0.02rem}
          }
          input:nth-of-type(1){
            margin-left: 5%;
          }
        }
        .type_list{
          display: flex;
          padding-bottom: 0.3rem;
          flex-wrap: wrap;
          li{
            width: 42%;
            height: 0.5rem;
            color: #333;
            font-size: 0.17rem;
            margin-left: 0.13rem;
            margin-top: 0.2rem;
            line-height: 0.5rem;
            text-align: center;
            background:rgba(229,232,235,0.5);
            border-radius:0.04rem;
          }
        }
        .bottom_type{
          position: fixed;
          bottom: 0;
          display: flex;
          width: 100%;
          font-size: 0.19rem;
          height: 0.47rem;
          button{
            flex: 1;
            text-align: center;
            line-height: 0.47rem;
          }
          button:nth-of-type(1){
            color: #3C7FEE;
            border: none;
            border-top: 1px solid #f5f5f5;
          }
          button:nth-of-type(2){
            color: white;
            background:linear-gradient(-90deg,rgba(60,127,238,1),rgba(103,154,237,1));
          }
        }
      }

    }
    .tab_list{
      display: flex;
      justify-content: space-between;
      height:0.47rem;
      width: 100%;
      background: white;
      li{flex: 1;line-height: 0.47rem;text-align: center;font-size: 0.19rem;
        display: flex;
        justify-content: center;
        i{font-style: normal}
        div{
          width: 0.3rem;
          display: flex;
          flex-direction: column;
          line-height: 0.34rem;
          span{
            font-size: 0.2rem;
            display: block;
            width: 0.3rem;
            color: #bcbcbc;
            height: 0.1rem;
          }
          .icon-shangsanjiao{
            font-size: 0.16rem;
          }

        }
      }
    }
    .jf_list{
      width: 3.5rem;
      background: white;
      margin: 0.17rem auto;
      border-radius: 0.04rem;
      flex: 1;
      li{
        border-bottom: 1px solid #f5f5f5;
        width: 3.42rem;
        height:0.8rem;
        margin: 0 auto;
        .jf_detail,.jf_data{
          display: flex;
          justify-content: space-between;
          .jfd_good{
            margin-top: 0.15rem;
            margin-left: 0.1rem;
          }
          .jfd_good,.jfd_year{
            font-size:.19rem;
            font-weight:400;
            color:rgba(51,51,51,1);
            margin-left: 0.12rem;
          }
          .jfd_year{
            font-size: 0.15rem;
            color: #9a9a9a;
            line-height: 0.4rem;
            span{
              margin-left: 0.13rem;
            }
          }
          .jfd_num{line-height: 0.43rem;color: #CC1C1C}
          .jfd_zhuchu{color: #CC1C1C}
          .jfd_num,.jfd_state{width: 30%;text-align: right;font-size: 0.17rem}
          .jfd_state{
            font-size: 0.13rem;
            color: #9a9a9a;
            line-height: 0.34rem;
          }
          .jfd_deal{
            color: #3C7FEE;
          }
        }
      }
    }
    .kefu{
      width: 0.52rem;
      height: 0.52rem;
      background:linear-gradient(-90deg,rgba(70,133,238,1),rgba(155,193,255,1));
      border-radius:50%;
      box-shadow:0px 5px 10px 0px rgba(60,127,238,0.42);
      position: fixed;
      bottom: 1.5rem;
      display: block;
      text-align: center;
      line-height: 0.52rem;
      font-size: 0.28rem;
      color: white;
      right: 0.11rem;
    }
    .invate{
      position: fixed;
      bottom: 0;
      width: 100%;
      height:0.47rem;
      font-size: 0.19rem;
      color: white;
      text-align: center;
      background:linear-gradient(-90deg,rgba(60,127,238,1),rgba(103,154,237,1));
    }
  }
</style>



